const GET_EVENTS = `
  query GetEvents {
    allEvents {
      id
      name
      eventName
      subEventName
      description
      location
      date
      time
      partners
      presenters
    }
  }
`;

function graphql(query, variables = {}) {
  return fetch('http://localhost:3000/admin/api', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      variables,
      query: GET_EVENTS,
    }),
  }).then(function(result) {
    return result.json();
  });
}

import Event from '@/components/Page/Event'
import NoEvent from '@/components/Error/NoEvent'

export default {

  components: {
    Event,
    NoEvent
  },

  data() {
    return {
      newEvent: '',
      search:''
    };
  },

// Get the event on server side
  async asyncData() {
    const { data } = await graphql(GET_EVENTS);
    return {
      events: data.allEvents,
    };
  },

  methods: {
    async getEvents() {
      const { data } = await graphql(GET_EVENTS);
      this.events = data.allEvents;
    },
  },

  mounted() {
    this.$nextTick(function() {
      window.addEventListener("scroll", function() {
        var navbar = document.getElementById("event-title-container")
        var nav_classes = navbar.classList
        if(document.documentElement.scrollTop >= 5) {
          if (nav_classes.contains("shrink") === false) {
            nav_classes.toggle("shrink");
          }
        }
        else {
          if (nav_classes.contains("shrink") === true) {
            nav_classes.toggle("shrink");
          }
        }
      })
    })
  },

  computed: {
    filteredEvents: function() {
      return this.events.filter((event) => {
        return event.eventName.match(this.search);
      });
    }
  }
};
